/* This stylesheet generated by Transfonter (https://transfonter.org) on March 11, 2018 8:27 PM */

$red: #DC0031;

@font-face {
    font-family: 'Univers LT Std';
    src: url('../fonts/UniversLTStd.woff2') format('woff2'),
        url('../fonts/UniversLTStd.woff') format('woff'),
        url('../fonts/UniversLTStd.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Univers LT Std';
    src: url('../fonts/UniversLTStd-BlackEx.woff2') format('woff2'),
        url('../fonts/UniversLTStd-BlackEx.woff') format('woff'),
        url('../fonts/UniversLTStd-BlackEx.ttf') format('truetype');
    font-weight: 900;
    font-style: normal;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Univers LT Std', 'Helvetica', sans-serif;
  background: #F3F3F3;
  font-size: 16px;
}

.page-content {
  display: flex;
  flex-direction: row;
}

.left, .right {
  width: auto;
}

.left {
  height: 100vh;
  position: fixed;
  left: 0;
  @media screen and (max-width: 960px) and (max-aspect-ratio: 5/4) {
    display: none;
  }
  .cover {
    height: 100%;
  }
  img {
    max-height: 100%;
    width: auto;
    padding: 2.5vw;
    box-sizing: border-box;
  }
}

.right {

  padding-left: 77vh;
  @media screen and (max-width: 960px) and (max-aspect-ratio: 5/4) {
    padding-left: 0;
  }
  width: 100%;
}

header {
  margin: auto;
  padding: 4vw 4vw 2vw;
}

.release {
  text-align: center;
}

.mag {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: auto;
  margin: 20px auto;
  width: 60%;
  .mag-cover,
  .mag-details {

    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    width: 50%;
    padding: 0 4%;
    min-width: 320px;
  }

  .mag-details {
    display: flex;
    flex-flow: column;

    .elements {
      margin: auto 20px;
    }
  }

  .mag-cover {
    display: none;

    @media screen and (max-width: 960px) and (max-aspect-ratio: 5/4) {
      display: block;
    }

    .cover {
      height: 100%;
      img {
        max-width: 100%;
        max-height: 100%;
        width: auto;
        height: auto;
      }
    }
    
  }
}

.signup {
  width: 50%;
  margin: 0 auto 50px;
  min-width: 300px;
  text-align: center;
}

h1 {
  display: block;
  width: 112px;
  height: 76px;
  font-size: 0;
  margin: auto;
  background: url('/logo.svg') no-repeat center;
  background-size: 100% auto;

  a {
    display: block;
  }
}

h2 {
  font-size: 13px;
  text-transform: uppercase;
  margin: 2em 0;
  color: black;
}

p {
  line-height: 1.75em;
}

form {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
}

#mc_embed_signup_scroll {
  width: 100%;
}

#mc_embed_signup div.mce_inline_error {
  background: none !important;
  color: $red !important;
  font-weight: normal !important;
}

#mce-success-response {
  padding: 0.5em;
}

.mc-field-group {
  width: auto;
  margin: auto;
  font-size: 0;
  text-align: center;

  * {
    font-size: 16px;
  }
}

input {
  box-sizing: border-box;
  font-family: 'Univers LT Std', 'Helvetica', sans-serif;
  height: 50px !important;
  line-height: 50px !important;
  font-size: 16px;
  border: 0 !important;
  padding: 0 1.25em;
  margin: 0.5em 0;
  text-align: left;
  outline: none;
  display: inline-block;
  background: white;
  -webkit-appearance: none;
  border-radius: 0 !important;

  &:focus {
    outline: none;
  }

  &[type="text"],
  &[type="email"] {
    width: 60%;
    min-width: 250px;
  }

  &[type="submit"] {
    width: auto;
    cursor: pointer;
    background: black;
    color: white;
    appearance: none;
    -webkit-appearance: none;
  }
}

